.card{
    display: flex;
    flex-direction: column;
    background-color: rgb(126, 205, 236);
    border-radius: 5px;
    border: 1px solid black;
    transform: scaleZ(0);
    cursor: pointer;
    padding: 25px;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;
}

.card:hover{
    transform: scale(1.05);
}